      padding-left: 0;
      padding-right: 0; } }

/**
 * Reset lateral padding for large monitor
 */
@media (min-width: 1200px) {
  .container--wide {
    padding-left: 6px;
    padding-right: 6px; } }

@media (max-width: 599px) {
  .expand-container {
    margin-left: -1.4rem;
    margin-right: -1.4rem; } }

.padding-tb_3 {
  padding: 3px 0; }

/**
 * Basic typography style for copy text
 */
html {
  color: #4b4d52;
  font: 14px/1.333 "Helvetica Neue", "Helvetica", "Arial", sans-serif; }
  @media (min-width: 992px) {
    html {
      font-size: 16px;
      line-height: 1.4; } }

header {
  background: #6fcd88;
  min-height: 300px;
  position: relative; }
  @media (min-width: 992px) {
    header {
      width: calc(2.5em + (100% - 2.5em)); } }
  header .header__txt {
    color: white;
    left: 50%;
    position: absolute;
    text-align: left;
    top: 50%;
    transform: translate(-50%, -50%); }
    header .header__txt .header__container .header__heading {
      font-size: 30px;
      font-weight: 700;
      padding: 10px 0; }
      @media (min-width: 992px) {
        header .header__txt .header__container .header__heading {
          font-size: 40px;
          line-height: 1.4; } }

.column-full {
  float: left;
  width: calc(2.5em + (100% - 2.5em)); }

.column-demi-half {
  float: left; }
  .column-demi-half:first-child {
    padding-bottom: 5px;
    padding-right: 5px;
    width: calc(2.5em + (100% - 2.5em)); }
    @media (min-width: 485px) {
      .column-demi-half:first-child {
        padding-bottom: 0;
        width: 33.33333%; } }
  .column-demi-half:last-child {
    text-align: left;
    width: calc(2.5em + (100% - 2.5em)); }
    @media (min-width: 485px) {
      .column-demi-half:last-child {
        text-align: right;
        width: 66.66667%; } }
  .column-demi-half.sm-half {
    width: calc(1em + ((100% - 2.5em) / 6 * 3)); }
    .column-demi-half.sm-half:last-child {
      text-align: right; }

.section__container {
  display: inline-block;
  padding: 25px 0;
  width: calc(2.5em + (100% - 2.5em)); }
  .section__container.bottom__line {
    border-bottom: 1px solid #ededeb; }
  .section__container .titles-left__txt {
    color: #aeafb1;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase; }
  .section__container .item-left__txt {
    font-size: 15px;
    font-weight: 400; }
  .section__container .item-right__txt {
    font-size: 15px;
    font-weight: 700; }
    .section__container .item-right__txt span {
      font-weight: 400; }
